@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Outfit','Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


input[type="text"] {
    background-color: transparent;
    border: 0;
    border-bottom: solid 2px #113a65;
    width: 50%;
    padding-bottom: 4px;
    color: #fff;
    font-weight: lighter;
    margin-bottom: 30px;
    margin-right: 20px;
    font-size: 20px;
    outline: none;
}

input::-webkit-input-placeholder {
    color: #c1cdda;
}
  
input::-moz-placeholder {
    color: #c1cdda;;
}

button {
    border: 2px solid #103a65;
    border-bottom: 4px solid #103a65;
    padding: 8px 20px;
    margin: 0 2px;
    border-radius: 2px;
    cursor: pointer;
    background-color: #235d9c;
    color: #fff;
    outline: none!important;
}
  
button:hover {
    border-bottom: 2px solid #103a65;
    position: relative;
    top: 2px;
}

button:active {
    border-bottom: 2px solid transparent;
    border-color: transparent;
    position: relative;
    top: 2px;
}
  
.error{
    color: red;
    width: 60%;
    font-size: 20px;
}
.weather {
    width: 60%;
    font-size: 20px;
    line-height: 23px;
    color: #fff;
}
.temp {
    font-size: 27px;
}
.info {
    height: 68vh;
    padding: 0 15px;
    background: url(/static/media/unnamed.17853e50.jpg) center center no-repeat;
    background-blend-mode: multiply;
    background-color: #484747;
    background-size: cover;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    color:  rgb(255 255 255 / 88%);
}

h1 {
    font-weight: bold;
    color: #fff;
}

h5 {
    margin-top: 10px;
    color: #e4e4e4;
    font-weight: bold;
}
.App {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(181,204,238,1);
  background: linear-gradient(to right, rgba(181,204,238,1) 0%, rgba(42,132,229,1) 99%, rgba(190,211,239,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#b5ccee', endColorstr='#bed3ef', GradientType=1 );
}

.main {
  margin: 0 auto;
  height: 68vh;
  width: 60%;
  display: flex; 
  background: #2a6bb1;
  box-shadow: 0px 2px 80px -20px rgba(0,0,0,0.5);
}

.formInfo {
  height: 68vh;
  padding-top: 55px;
  padding-left: 50px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

@media(max-width: 1200px){
  .main{
    width: auto
  }
  .formInfo{
    padding-top: 50px;
  }
  .weather{
    font-size: 19px;
    line-height: 19px;
  }
}
@media(max-width: 575px) {
  .App {
    align-items: normal;
  }
  .main {
    height: auto;
    display: flex;
    flex-direction: column;
  }
  .info {
    height: auto;
    display: block;
    align-items: normal;
    padding: 15px 0;
  }
  .formInfo {
    height: auto;
    width: 100%;
    padding-top: 34px;
  } 
  .weather{
    margin-top: 0;
    width: auto;
    font-size: 18px;
    line-height: 17px;
  }
}
